@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,menu,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,main,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,main,menu,nav,section{display:block}*[hidden]{display:none}body{line-height:1}menu,ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}*{box-sizing:border-box}button{font-family:inherit}body{background-color:#262d3a;font-family:"Lato",sans-serif;font-size:1rem}#root{display:flex;flex-direction:column;min-height:100vh}

main{background-color:#fff;flex:1 1}.main-container{margin:0 auto;max-width:1440px;padding:16px}.skip-to-main:not(:focus):not(:active){clip:rect(1px, 1px, 1px, 1px);height:1px;overflow:hidden;position:absolute;white-space:nowrap;width:1px}.skip-to-main:focus,.skip-to-main:active{background-color:#eee67a;border:1px solid #262d3a;color:inherit;left:8px;padding:16px;position:absolute;text-decoration:none;top:8px}

.gallery{display:grid;grid-gap:16px}@media (max-width: 1023px){.gallery{grid-template-areas:"title" "filters" "results"}}@media (min-width: 1024px){.gallery{grid-template-columns:25%;grid-template-areas:"title title" "filters results"}}.gallery__card-container{display:flex;flex-wrap:wrap;justify-content:space-evenly}.gallery__filters{grid-area:filters}@media (max-width: 1023px){.gallery__filters{border-bottom:1px solid #262d3a}}@media (min-width: 1024px){.gallery__filters{border-right:1px solid #262d3a}}.gallery__results{display:flex;grid-area:results;justify-content:center}@media (min-width: 1024px){.gallery__results{align-items:center;flex-direction:column}}.gallery__title{font-size:2rem;grid-area:title;margin:16px 0;text-transform:uppercase}

.card{box-shadow:0 0 8px 0 rgba(0,0,0,0.5);border-radius:8px;margin:16px 8px;max-width:calc( var(--width-image) + 34px);padding:16px}@media (max-width: 1023px){.card{--width-image: 225px}}@media (min-width: 1024px){.card{--width-image: 280px}}.card tr:not(:last-child){border-bottom:1px solid #262d3a}.card tr td{padding:8px}.card tr td:first-letter{text-transform:capitalize}.card tr td:last-child{width:100%}.card__episodes-button,.card__location-button,.card__origin-button{border:none;background-color:inherit;cursor:pointer;font-size:inherit;padding:0;text-align:left;text-decoration:underline}.card__episodes-label,.card__location-label,.card__origin-label{text-transform:capitalize}.card__episodes-label--hidden:not(:focus):not(:active),.card__location-label--hidden:not(:focus):not(:active),.card__origin-label--hidden:not(:focus):not(:active){clip:rect(1px, 1px, 1px, 1px);height:1px;overflow:hidden;position:absolute;white-space:nowrap;width:1px}.card__image{display:inherit;transform:translateZ(0);transition:transform 250ms ease-out;width:var(--width-image)}@media (min-width: 1024px){.card__image:hover{transform:scale(1.07143)}}.card__image-container{border:1px solid #262d3a;margin-bottom:16px;overflow:hidden}.card__title{font-size:1.5rem;margin-bottom:16px;text-align:center}.card__status{align-items:center;display:flex;text-transform:capitalize}.card__status-icon{border-radius:50%;display:inline-block;margin-right:8px;height:0.5rem;width:0.5rem}.card__status-icon--alive{background:#5c4}.card__status-icon--dead{background:#d63d2e}

.episodes{width:100%}.episodes__cell{padding:8px}.episodes__cell--left{text-align:left;text-transform:uppercase}.episodes__row--border,.episodes__row:not(:last-child){border-bottom:1px solid #262d3a}

.table-location{width:100%}.table-location td{padding:8px;text-transform:capitalize}.table-location tr:not(:last-child){border-bottom:1px solid #262d3a}

.filters{margin-top:8px;padding-bottom:16px;padding-top:16px}@media (min-width: 1024px){.filters{padding-right:24px;position:-webkit-sticky;position:sticky;top:0}}.filters__reset{margin-bottom:16px}.filters__title{font-size:1.5rem}

.input{margin:calc(16px + 2rem) 0;position:relative}.input__field{border-bottom:1px solid #262d3a;border-left:none;border-right:none;border-top:none;display:block;font-size:1rem;padding:16px;width:100%}.input__field:focus ~ .input__label{top:-1rem;font-size:.8rem}.input__label{font-size:1rem;left:16px;pointer-events:none;position:absolute;top:16px;transition:250ms ease all}.input__label--shrink{top:-1rem;font-size:.8rem}

.select{border-bottom:1px solid #262d3a;margin:calc(16px + 2rem) 0;padding-right:16px;position:relative}.select__field{border:none;display:block;font-size:1rem;padding:16px;text-transform:capitalize;width:100%}.select__label{font-size:1rem;left:16px;pointer-events:none;position:absolute;top:16px;transition:250ms ease all;top:-1rem;font-size:.8rem}

.button{background-color:#eee67a;border:1px solid #262d3a;cursor:pointer;font-size:1rem;min-width:48px;padding:16px;text-transform:uppercase;transition:background-color 250ms linear,border 250ms linear,color 250ms linear}.button:hover{background-color:#262d3a;border:1px solid #eee67a;color:#fff}

.pagination{text-align:center}@media (min-width: 768px){.pagination{margin:16px}}.pagination__button{border:none;border-radius:50%;background-color:transparent;font-size:1rem;margin:8px;min-width:48px;padding:16px;transition:background-color 250ms linear}.pagination__button:hover:not(.pagination__button--disabled),.pagination__button--active:not(.pagination__button--disabled){background-color:#eee67a}.pagination__button:not(.pagination__button--active):not(.pagination__button--disabled){cursor:pointer}@media (max-width: 1023px){.pagination__container{position:-webkit-sticky;position:sticky;top:0}}

.footer{box-shadow:0 -8px 8px 0 rgba(0,0,0,0.5)}.footer__container{margin:0 auto;max-width:1440px;align-items:center;color:white;display:flex;flex-shrink:0;justify-content:space-between;padding:16px;width:100%}.footer__copyright{font-size:.8rem}.footer__icons svg{height:38px;width:38px}.footer__icon{padding:5px}.footer__icon-caption--hidden:not(:focus):not(:active){clip:rect(1px, 1px, 1px, 1px);height:1px;overflow:hidden;position:absolute;white-space:nowrap;width:1px}

.header{box-shadow:0 8px 8px 0 rgba(0,0,0,0.5);height:100px;padding:16px;text-align:center;width:100%;z-index:2}.header__figure{display:inline-block}.header__figcaption:not(:focus):not(:active){clip:rect(1px, 1px, 1px, 1px);height:1px;overflow:hidden;position:absolute;white-space:nowrap;width:1px}.header__logo{height:calc(100px - 32px);stroke:#eee67a;stroke-opacity:0.8;stroke-width:2px}.header__title:not(:focus):not(:active){clip:rect(1px, 1px, 1px, 1px);height:1px;overflow:hidden;position:absolute;white-space:nowrap;width:1px}

.modal{background-color:#fff;border:1px solid #262d3a;border-radius:2px;margin:auto;max-width:60ch;padding:24px;position:relative}.modal__close{border:none;background-color:inherit;cursor:pointer;font-size:1.5rem;margin-left:16px}.modal__content{margin-top:16px}.modal__overlay{background-color:rgba(0,0,0,0.7);bottom:0;display:flex;left:0;position:fixed;overflow:auto;right:0;top:0;z-index:3}.modal__title{align-items:center;display:flex;font-size:1.5rem;justify-content:space-between}

